// 
interface CardDataItemBase {
    megami: Megami;
    name: string;
    ruby?: string;
    types: CardType[];
    range?: string;
    damage?: string;
    capacity?: string;
    text: string;
}

interface NormalCardDataItem extends CardDataItemBase {
    baseType: 'normal';
}

interface SpecialCardDataItem extends CardDataItemBase {
    baseType: 'special';
    cost?: string;
}

type CardDataItem = NormalCardDataItem | SpecialCardDataItem;

