// socket.io
export namespace SocketParam {
    export type appendActionLog = {boardId: string, log: LogRecord};
    export type bcAppendActionLog = {log: LogRecord};

    export type appendChatLog = {boardId: string, log: LogRecord};
    export type bcAppendChatLog = {log: LogRecord};
}

// 
@Serialize({})
export class LogRecord extends Serializable {
    @SerializeProperty()
    body: string;
    @SerializeProperty()
    created: string;
    @SerializeProperty()
    speaker?: string;

    constructor(){
        super();
        this.created = moment().format();
    }
}

@Serialize({})
export class Card extends Serializable {
    @SerializeProperty()
    id: string;
    @SerializeProperty()
    used?: boolean; // 
    @SerializeProperty()
    sakuraToken?: number; // 
    @SerializeProperty()
    type: string;

    constructor(id: string){
        super();
        this.id = id;
    }

    get data(): CardDataItem{
        return CARD_DATA[this.id];
    }
}

@Serialize({})
export class SpecialCard extends Card {
    @SerializeProperty()
    used: boolean = false;
}

@Serialize({})
export class BoardSide extends Serializable {
    @SerializeProperty()
    playerName: string = null;

    @SerializeProperty({list: true})
    megamis: Megami[] = [];

    @SerializeProperty()
    aura: number = 3;
    @SerializeProperty()
    life: number = 10;
    @SerializeProperty()
    flair: number = 0;

    @SerializeProperty()
    vigor: number = 0;

    @SerializeProperty({list: true, type: Card})
    library: Card[] = [];
    @SerializeProperty({list: true, type: Card})
    hands: Card[] = [];
    @SerializeProperty({list: true, type: Card})
    used: Card[] = [];
    @SerializeProperty({list: true, type: Card})
    hiddenUsed: Card[] = [];
    @SerializeProperty({list: true, type: Card})
    specials: Card[] = [];
}


@Serialize({})
export class Board extends Serializable {

    @SerializeProperty()
    dataVersion = 1;

    @SerializeProperty()
    distance: number = 10;
    @SerializeProperty()
    dust: number = 0;
    @SerializeProperty()
    tokensOnCard: {[cardId: string]: number} = {};

    @SerializeProperty({list: true, type: LogRecord})
    actionLog: LogRecord[] = [];
    @SerializeProperty({list: true, type: LogRecord})
    chatLog: LogRecord[] = [];

    @SerializeProperty({type: BoardSide})
    p1Side: BoardSide;
    @SerializeProperty({type: BoardSide})
    p2Side: BoardSide;

    constructor(){
        super();
        this.p1Side = new BoardSide();
        this.p2Side = new BoardSide();
    }

    getMySide(side: Side){
        if(side === 'p1'){
            return this.p1Side;
        } else if(side === 'p2'){
            return this.p2Side;
        }
        return null;
    }

    getOpponentSide(side: Side){
        if(side === 'p1'){
            return this.p2Side;
        } else if(side === 'p2'){
            return this.p1Side;
        }
        return null;
    }
}


